.list {
    margin-top: 30px;
    color: rgb(248, 244, 244);
    font-weight: 500;
    font-size: 18px;
    transition: transform 250ms;
  }
  
  .item {
    display: flex;
    justify-content: space-between;
  }
  
  .item:not(:last-child) {
    margin-bottom: 10px;
  }
  
  .btn {
    width: 30px;
    height: auto;
    color: white;
    cursor: pointer;
    display: inline-block;
    background-color: inherit;
    border: none;
    outline: none;
    transition: transform 250ms;
  }
  
  .btn:hover {
    transform: scale(2.1);
    color: red;
  }